<template>
  <v-card>
    <div class="d-flex flex-wrap justify-center">
      <v-badge
        v-for="(st,index) in students"
        :key="index"
        bordered
        bottom
        color="deep-purple accent-4"
        offset-x="20"
        offset-y="20"
        icon="mdi-check"
        :value="st.checked == true"
      >
        <v-avatar class="ma-1" size="70" color="blue" @click="stOnClick(st)">
          <span class="text-body-1">{{st.name}}</span>
        </v-avatar>
      </v-badge>
    </div>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      students: [],
    };
  },
  created() {
    this.students.push(
      ...Array.from(Array(60)).map((_,index) => ({
        name: "學生名稱",
        checked: index < 10 ? true : false,
      }))
    );
  },
  methods: {
    stOnClick(st) {
      st.checked = !st.checked;
    },
  },
};
</script>

